.button {
  width: 100%;
  padding: 1rem;
  margin-top: 1.5rem;
  background-image: linear-gradient(to bottom left, #e14eca, #ba54f5, #e14eca);
  border: none;
  border-radius: 10px;
  font-size: 16px;
  color: #eee;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.5s;
  margin-top: 50px;
}

.roxo {
  background-image: linear-gradient(to bottom left, #e14eca, #ba54f5, #e14eca);
}
.azul {
  background: linear-gradient(to right bottom, #6d4cff, #2c06ff, #6d4cff);
}

.button:disabled {
  opacity: 0.7;
  cursor: wait;
}

.button:hover {
  transform: translateY(-5%);
}
