.container {
    position: fixed;
    top: 200px;
    right: 2px;
    background-color: rgba(0, 0, 0, 0.384);
    width: 60px;
    height: 80px;
    border-radius: 3px;
    font-size: 16px;
    font-family: Poppins, sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    justify-content: center;
    cursor: pointer;
    z-index: 750;
}