.card {
    background-color: #f3f5f7;
    /* border: 2px solid #ee2d3d; */
    width: 300px;
    /* height: 230px; */
    border-radius: 10px;
    overflow: hidden;
    cursor: pointer;
    transform: scale(.9);
    transition: all .3s ease;
    display: flex;
    flex-direction: column;
    box-shadow: 0px 0px 3px 5px #6d4cff1c;
}



.card:hover{
    transform: scale(1);

}

.card img{
    width: 100%;
    margin: 0;
}

.infos{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 .5rem;
    color: #261909;
    flex: 1;
    margin: 0;
    font-family: Poppins, sans-serif;
    font-weight: 600;
}

.card h2 {
    margin: 0;
}

.card button{
    padding: .5rem 1rem;
    border: 1px solid #261909;
    background: none;
    width: 50%;
    margin: 10px auto;
    color: #261909;
    border-radius: 30px;
    cursor: pointer;
    transition: all .3s;
}
.card button:hover{
    color: #f3f5f7;
    background: #261909;
    transform: scale(1.2);
}

.card div.promo{
    color: #261909;
    text-align: center;
    font-size: 14px;
    font-family: Poppins, sans-serif;
}

.noStock{
    opacity: 0.3;   
    cursor: inherit;

}

.card.noStock button:hover{
    color: #261909;
    background: none;
    transform: scale(1);
}