.card {
    border-top: 1px solid rgba(87, 87, 87, 0.521);
    height: 80px;
    margin: auto;
    display: flex;
}

.values {
    display: grid;
    grid-template-columns: 80px repeat(7, 110px);
    /* margin-left: 2rem; */
    height: 100%;
    align-items: center;
    font-family: Poppins, sans-serif;
    color: #eee;
    font-size: 14px;
}

.values div{
    padding-right: 15px;
}

.btn{
    width: 30px;
    height: 30px;
    margin-right: 10px;
    background: linear-gradient(to right bottom ,#e14eca,#ba54f5,#e14eca);;
    transform: translateY(calc(40px - 50%));
    color: #eee;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    cursor: pointer;
    transition: all .3s;
}

.btn:hover{
    transform: translateY(calc(40px - 60%));
}