.info {
  font-family: sans-serif;
  color: #aaa;
  display: grid;
  grid-template-columns: 50px repeat(2, 110px) 200px repeat(2, 40px);
  font-size: 14px;
  margin: 1rem auto;
  max-width: calc(80px + 10 * 110px + 40px);
  padding-left: 2.5rem;
  font-weight: bold;
}

.info input {
  border-left: 0px solid #ba54f5;
  padding-left: 5px;
  background: none;
  border: none;
  outline: none;
  color: #bbb;
  /* font-weight: 600; */
  font-family: Poppins;
  height: 16px;
}

.info div:last-child {
  display: flex;
  align-items: center;
  height: 16px;
}

.info div:last-child svg {
  cursor: pointer;
  width: 20px;
  height: 20px;
}

.tampa {
  width: 200px;
  /* backdrop-filter: blur(1px); */
  background-color: rgba(255, 255, 255, 0.767);
  backdrop-filter: blur(5px);
  color: #333;
  font-size: 12px;
  display: flex;
  justify-content: center;
  z-index: 900;
  position: absolute;
  transform: translateY(-0%);
  padding: 10px 0;
  border: 1px solid #ba54f5;
  cursor: pointer;
}

#copyBtn svg {
  cursor: pointer;
  transition: all 0.3s;
}
#copyBtn.copied svg {
  fill: #0f0;
}

@keyframes animateCopy {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.3);
  }
  100% {
    transform: scale(1);
  }
}

.new {
  font-size: 10px;
  padding: 3px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.new div {
  background-color: #ba54f5;
  text-align: center;
  padding: 3px;
  border-radius: 10px;
}

.modalDelContainer {
  position: fixed;
  backdrop-filter: blur(10px);
  top: 0;
  left: 0;
  z-index: 999;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modalDel {
  animation: into 0.5s forwards;
  background-color: #171941;
  padding: 1rem;
  box-shadow: 0px 0px 4px 2px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  color: #eee;
  font-family: 'Poppins', sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.title {
  margin-bottom: 15px;
  font-weight: 900;
  font-size: 1.3rem;
}

.values svg {
  cursor: pointer;
}

.btns button:first-child {
  background-image: linear-gradient(
    to bottom left,
    #ff2727,
    rgb(201, 1, 1),
    #ff2727
  );
}

.modalDel button {
  margin: 5px 0;
}

@keyframes into {
  from {
    transform: scale(0.1);
  }
  to {
    transform: scale(1);
  }
}
