.btn {
    display: flex;
    flex-direction: column;
    gap: 5px;
    position: absolute;
}


section div nav .btn div{
    width: 25px;
    height: 25px;
    background-color: #ba54f550;
    border-radius: 50%;
    z-index: 99; 
    display: block;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 2rem;
    color: #eee;
    font-weight: 100;
    transition: all .2s;
    cursor: pointer;
    max-width: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
}
section div nav .btn div:first-child{
    display: block;
    text-align: center;
    line-height: 26px;
}

.btn.active div:first-child{
    transform: rotateZ(45deg);
}

.inactive{
    display: none;
}


