.modal{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    z-index: 700;
    display: flex;
    justify-content: center;
    align-items: center;
    backdrop-filter: blur(13px);
}

.card{
    animation: intro .3s forwards ;
    max-width: 500px;
    background-color: #171941;
    font-family: Poppins;
    display: flex;
    flex-direction: column;
    padding: 1rem;
    border-radius: 10px;
    z-index: 999;
    border: 1px solid #e14eca;
    box-shadow: 0px 0px 25px 45px rgba(0, 0, 0, 0.25);
}

.card div{
    font-size: 20px;
    color: #eee;
    margin: 10px 0;
}

.card button{
    margin: 5px 0;
}

@keyframes intro {
    from {
        transform: scale(.3);
    }
    
    to {
        transform: scale(1);
    }
}