@import url('https://css.gg/user.css');
@import url('https://css.gg/lock.css');

.inputContainer {
    width: 100%;
    max-height: 3rem;
    margin: auto;
    display: block;
    color: #eee;
    overflow: hidden;
    background: none;
    border: solid rgba(255, 255, 255, .7);
    border-width: .5px;
    margin: 5px 0;
    border-radius: 10px;
    display: flex;
}
.icon{
    width: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-repeat: no-repeat;
    background-position: center ;
}
.input{
    width: 100%;
    background: none;
    outline: none;
    border: none;
    color: white;
    padding: .8rem .1rem;
    color: #eee;
}
