.container {
  margin: 1rem;
  width: calc(100% - 2rem);
  display: flex;
  flex-direction: column;
  overflow-x: scroll;
}

.container::-webkit-scrollbar {
  /* width: 5px; */
  background: #00000000;
  height: 5px;
}

.container::-webkit-scrollbar-thumb {
  background-color: #ba54f5;
  border-radius: 30px;
}

.containerTitle {
  border-bottom: 1px solid rgba(87, 87, 87, 0.521);
  display: flex;
  justify-content: center;
}

.titles {
  font-family: sans-serif;
  color: #aaa;
  display: grid;
  grid-template-columns: 50px repeat(2, 110px) 200px repeat(2, 40px);
  font-size: 14px;
  margin: 2rem auto;
  max-width: calc(80px + 10 * 110px + 40px);
  padding-left: 2.5rem;
  font-weight: bold;
  /* border-bottom: 1px solid #ba54f5; */
  /* padding: 1rem 0; */
}
