.card {
  border-top: 1px solid rgba(87, 87, 87, 0.521);
  height: 80px;
  margin: auto;
  display: flex;
}

.values {
  display: grid;
  grid-template-columns: 50px 200px 100px 100px 150px 150px 100px 40px 40px;

  height: 100%;
  align-items: center;
  font-family: Poppins, sans-serif;
  color: #eee;
  font-size: 14px;
  padding-left: 40px;
}

.values div {
  padding-right: 15px;
}

.btn {
  width: 30px;
  height: 30px;
  margin-right: 10px;
  background: linear-gradient(to right bottom, #e14eca, #ba54f5, #e14eca);
  transform: translateY(calc(40px - 50%));
  color: #eee;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s;
}

.btn:hover {
  transform: translateY(calc(40px - 60%));
}

#copyBtn svg {
  cursor: pointer;
  transition: all 0.3s;
}
#copyBtn.copied svg {
  fill: #0f0;
}

@keyframes animateCopy {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.3);
  }
  100% {
    transform: scale(1);
  }
}

.new {
  font-size: 10px;
  padding: 3px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.new div {
  background-color: #ba54f5;
  text-align: center;
  padding: 3px;
  border-radius: 10px;
}

.modalDelContainer {
  position: fixed;
  backdrop-filter: blur(10px);
  top: 0;
  left: 0;
  z-index: 999;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modalDel {
  animation: into 0.5s forwards;
  background-color: #171941;
  padding: 1rem;
  box-shadow: 0px 0px 4px 2px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  color: #eee;
  font-family: 'Poppins', sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.title {
  margin-bottom: 15px;
  font-weight: 900;
  font-size: 1.3rem;
}

.values svg {
  cursor: pointer;
}

.btns button:first-child {
  background-image: linear-gradient(
    to bottom left,
    #ff2727,
    rgb(201, 1, 1),
    #ff2727
  );
}

.modalDel button {
  margin: 5px 0;
}

@keyframes into {
  from {
    transform: scale(0.1);
  }
  to {
    transform: scale(1);
  }
}
