.container{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.borderB{
    width: 95%;
    border-bottom: 1px solid rgba(87, 87, 87, 0.521);
    height: 70px;
    display: flex;
    justify-content: center;

}

.values {
    color: #aaa;
    display: grid;
    grid-template-columns: 150px 50px;
    font-family: Poppins;
    margin: 1rem auto;
    padding: 1rem;
    height: 70px;
}