.container {
  margin: 1rem;
  display: flex;
  flex-direction: column;
  overflow-x: scroll;
  width: calc(100% - 2rem);
  box-sizing: border-box;
}

.container::-webkit-scrollbar {
  background: #00000000;
  height: 5px;
}

.container::-webkit-scrollbar-thumb {
  background-color: #ba54f5;
  border-radius: 30px;
}

.titles {
  font-family: sans-serif;
  color: #aaa;
  display: grid;
  grid-template-columns: 50px 200px 200px 100px 100px 100px 150px 150px 100px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  padding: 2rem;
  font-size: 14px;
  width: calc(
    50px + 200px + 200px + 100px + 100px + 100px + 150px + 150px + 100px + 40px +
      100px
  );
  /* max-width:  */
  padding-left: 2.5rem;
  font-weight: bold;
}
