@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@900&display=swap');

.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 90%;
    padding-top: 2rem;
    min-height: 100vh;
}

.loginForm{
    background-color: #171941;
    width: 100%;
    align-items: center;
    border-radius: 10px;
    overflow: hidden;
    padding: 0rem .5rem;
    box-shadow: 0px 0px 3px 8px rgba(0, 0, 0, 0.185);
    position: relative;
    max-width: 450px;
}

.loginForm form{
    flex-direction: column;
    justify-content: center;
    align-items: center;
    display: flex;
    width: 100%;
}
.card{
    width: 100%;
    transform: translate(-30%, -40%);
    height: 200px;
    border-radius: 30px;
    transition: all .3s;
    background: linear-gradient(to right bottom ,#6d4cff,#2c06ff,#6d4cff);
}
.title{
    position: relative;
    font-size: 3rem;
    font-family: Poppins, Helvetica, sans-serif;
    color: #eee;
    margin-bottom: -125px;
    top: -190px;
    font-weight: bolder;
    transition: all .3s;
}

.crie {
    color: #eee;
    font-family: Arial, Helvetica, sans-serif;
    text-align: center;
    font-size: 12px;
    font-weight: bold;
}
.crie span{
    color: #6d4cff;
}

.crie a {
    color: currentColor;
    text-decoration: none;
}

.twoItens {
    display: grid;
    width: 100%;
    grid-template-columns: auto auto;
    padding: 0;
    gap: 5px;
}

@media (max-width: 350px){
    .twoItens{
        grid-template-columns: auto;
    }
    .title{
        font-size:  1.5rem;
    }
    .card{
        transform: translate(-30%, -60%);
    }
}

.redes{
    margin-top: 2rem;
    width: 80%;
    max-width: 1000px;
    display: flex;
    justify-content: space-evenly;
    color: #eee;
    font-family: Poppins, sans-serif;
    box-shadow: 0px 0px 4px 5px #00000033;
    padding: 1rem;
    border-top-left-radius: 30px;
    border-top-right-radius: 30px;
}

.redes a{
    text-align: center;
    cursor: pointer;
    text-decoration: none;
    color: #eee;
}

.redes a:first-child svg{
    color: #34af23;
}

.redes a div:last-child{
    color: #ba54f5;
}

.redes a:last-child svg{
    color: #0088cc;
}

.keepLogin{
    color: #eee;
    display: flex;
}

.captcha{
    margin-top: 1rem;
    margin-bottom: -1.5rem ;
}