.container {
    margin: 1rem;
    width: calc(100% - 2rem);
    display: flex;
    flex-direction: column;
    overflow-x: scroll;
}

.container::-webkit-scrollbar{
    background: #00000000;
    height: 5px;
}

.container::-webkit-scrollbar-thumb {
    background-color: #ba54f5;
    border-radius: 30px;
}

.titles {
    font-family: sans-serif;
    color: #aaa;
    display: grid;
    grid-template-columns: 80px repeat(11, 110px);
    font-size: 14px;
    margin: 2rem auto;
    max-width: calc(80px + 11 * 110px + 40px);
    padding-left: 2.5rem;
    font-weight: bold;
}

.err{
    text-align: center;
    color: #bbb;
    font-family: Poppins;
    font-size: 1.2rem;
}

.nav{
    display: flex;
    margin: auto;
}

.nav div{
    font-family: Poppins;
    width: 25px;
    height: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 5px;
    color: #eee;
    border-radius: 2px;
    background-color: #ba54f53b;
    transition: all .3s;
    cursor: pointer;
}

.nav a {
    text-decoration: none;
}

.nav div.active{
    background-color: #ba54f5;
    transform: scale(1.1);
}

.search{
    margin: 1rem auto;
    display: block;
    text-align: center;
}

.search input{
    padding: .5rem 1rem;
    border-radius: 20px;
    /* border: 1px solid #ba54f5; */
    border: none;
    background: none;
    color: #eee;
    box-shadow: 1px 0px 5px 1px #ba54f563;
    outline: none;
    backdrop-filter: blur(10px);
    font-family: Poppins, sans-serif;
}