.container {
  margin: 1rem;
  width: calc(100% - 2rem);
  display: flex;
  flex-direction: column;
  overflow-x: scroll;
}

.container::-webkit-scrollbar {
  background: #00000000;
  height: 5px;
}

.container::-webkit-scrollbar-thumb {
  background-color: #ba54f5;
  border-radius: 30px;
}

.titles {
  font-family: sans-serif;
  color: #aaa;
  display: grid;
  grid-template-columns: 50px 200px 100px 100px 150px 150px 100px 40px 40px;

  font-size: 14px;
  margin: 2rem auto;
  max-width: calc(80px + 11 * 110px + 40px);
  padding-left: 2.5rem;
  font-weight: bold;
}
