.content {
  margin: 0 1rem;
  display: flex;
  flex-direction: column;
  overflow-x: scroll;
}

.content::-webkit-scrollbar {
  background: #00000000;
  height: 5px;
}

.content::-webkit-scrollbar-thumb {
  background-color: #ba54f5;
  border-radius: 30px;
}

.titles {
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  padding: 1.5rem 0;
  font-family: sans-serif;
  color: #aaa;
  display: grid;
  grid-template-columns: 50px 110px 250px repeat(4, 110px) 40px 40px;

  font-size: 14px;
  margin: 10px auto;
  max-width: calc(80px + 12 * 110px + 40px);
  padding-left: 2.5rem;
  font-weight: bold;
  /* border-bottom: 1px solid #ba54f5; */
  /* padding: 1rem 0; */
}

.err {
  font-family: Poppins;
  text-align: center;
  font-size: 1.5rem;
  color: #aaa;
  margin: 2rem;
}
