@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;900&display=swap');


.header{
    height: 100px;
    display: grid;
    width: 100%;
    color: #eee;
    justify-content: space-between;
    grid-template-columns: 1fr 1fr 1fr;
    align-items: center;
    z-index: 800;
}

.logo{
    font-family: Poppins;
    text-align: center; 
}

.logo h2{
    font-size: 2rem;
    margin: 0;
    padding: 0;
    font-weight: bold;
}

.logo h2 span{
    background-color: #ba54f5;
}

.logo p{
    margin: 0;
    font-size: 12px;
    text-transform: uppercase;
}

.txt{
    font-size: 16px;
    font-family: Arial, Helvetica, sans-serif;
}

.btns{
    text-align: end ;
    display: flex;
    justify-content: flex-end;
}

.user{
    display: inline-block;
    z-index: 500;
    font-family: Arial, Helvetica, sans-serif;
    cursor: pointer;
    display: flex;
    align-items: center;
}

.user::after{
    display: inline-block;
    margin-left: .255em;
    position: relative;
    vertical-align: .255em;
    content: '';
    border-top: .3em solid;
    border-right: .3em solid transparent;
    border-bottom: 0;
    border-left: .3em solid transparent;
    transition: all .3s;
}

.logo img {
    max-width: 170px;
}

.user.active::after{
    display: inline-block;
    margin-left: .255em;
    vertical-align: .255em;
    content: '';
    border-top: .3em solid;
    border-right: .3em solid transparent;
    border-bottom: 0;
    border-left: .3em solid transparent;
    transform: rotateZ(180deg);
    z-index: 999;
}

.nav {
    position: absolute;
    top: 65px;
    width: 150px;
    background-color: #eee;
    color: #333;
    /* right: 40px; */
    transition: all .3s;
    height: 0px;
    display: flex;
    overflow: hidden;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    z-index: 500;
}
.nav ul{
    width: 100%;
    margin: 0;
    padding: 0;

}
.nav li{
    list-style: none;
    text-align: center;
    width: 100%;
    margin: 0;
    padding: .4rem 0;
    font-family: Poppins;
    font-weight: 400;
    cursor: pointer;
}

.header a{
    color: currentColor;
    text-decoration: none;
}
.nav.active{
    height: 100px;
}

@media (max-width: 680px) {
    .header{
        display: flex;
    }

    .txt{
        display: none;
    }
}

@media (max-width: 450px){
    .logo h2{
        font-size: 16px;
        margin: 0;
        padding: 0;
        font-weight: bold;
    }
    
    .logo h2 span{
        background-color: #ba54f5;
    }
    
    .logo p{
        margin: 0;
        font-size: 0px;
        text-transform: uppercase;
    }
    
    .txt{
        font-size: 8px;
        font-family: Arial, Helvetica, sans-serif;
    }
}

@media (max-width: 350px){
    .logo h2{
        font-size: 16px;
        margin: 0;
        padding: 0;
        font-weight: bold;
    }
    
    .logo img {
        max-width: 100px;
    }

        .user{
            font-size: 12px;
        }

}