.container{
    overflow-x: hidden;
}

.title{
    width: calc(100% - 20px);
    margin: 10px;
    padding: 3px 10px;
    color: #eee;
    font-family: Poppins;
    background: #ba54f5;
    box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.4);
}

.cards {
    display: flex;
    width: 100%;
    justify-content: space-around;
    flex-wrap: wrap;
}

.cards .item {
    width: 150px;
    height: 150px;
    text-align: center;
    color: #eee;
    font-family: Poppins;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    /* box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.4) ; */
    box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.1) inset,
                0px 0px 5px 1px rgba(0, 0, 0, 0.4) ;
    margin: 10px;
    border-radius: 10px;
    align-items: center;
    backdrop-filter: blur(10px);
    padding: 10px;
}

.cards .item div:nth-child(2){
    font-size: 18px;
    font-weight: 900;
}

.scroll{
    width: calc(100% - 2rem);
    margin: 1rem;
    height: 500px;
    box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.4);
    border-radius: 1rem;
    backdrop-filter: blur(5px);
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 1rem;
    overflow: auto;
    
}

.feed {
    padding: 5px;
    overflow-x: hidden;
}

.scroll div{
    color: #eee;
    font-family: Poppins, sans-serif;
    font-size: 14px;
    height: 1rem;
    width: max-content;
    display: flex;
    gap: 1rem;
    margin: 2px 0;
    /* overflow: ; */
}

.scroll::-webkit-scrollbar{
    background: #00000000;
    height: 5px;
    width: 5px;
}

.scroll::-webkit-scrollbar-thumb {
    background-color: #ba54f5;
    border-radius: 30px;
}

.scroll::-webkit-scrollbar-corner{
    background: none;
    color: none;
}