.title {
    margin: 1rem;
    font-size: 2rem;
    color: #ddd;
}

.title::after{
    content: '';
    width: 150px;
    height: 3px;
    z-index: -1;
    position: relative;
    background-color: #ba54f5;
    border-radius: 2px;
    display: block;
    top: 5px;
    margin-bottom: 10px;
}

.containerAlterar{
    margin: 2rem;
    display: flex;
    flex-direction: column;
    max-width: 400px;
    box-sizing: border-box;

}

.subTitle{
    margin: 1rem 0;
    color: #ccc;
    font-size: 18px;
}

.containerAlterar button{
    margin-top: 1rem;
    margin: 1rem 0;
    box-sizing: border-box;
}
