.card {
    margin: auto;
    width: 100%;
    height: 150px;
    display: flex;
    background-color: #33399d;
    border-bottom: 2px solid #0c9eff;
    border-radius: 5px;
    box-sizing: border-box;
    align-items: center;
    padding: 0 2rem;
    justify-content: space-between;
    font-size: 16px;
}

.card img {
    /* width: 25%; */
    max-width: 150px;
}

.infos {
    font-family: Poppins;
    color: #aaa;
    display: flex;
    flex-direction: column;
}

.infosBtn{
    display: flex;
    align-items: center;
    justify-content: space-between;
    /* margin: 10px 0; */
}

.limite {
    color: #fff;
    font-size: .7em;
    background-color: #ba54f5;
    display: inline-block;
    border-radius: 5px;
    padding: 0 2px;
}

.btnBuy{
    /* background: linear-gradient(to botom, #ba54f5, #e14eca, #ba54f5); */
    background: linear-gradient(to right bottom ,#e14eca,#ba54f5,#e14eca);
    border-radius: 50%;
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #eee;
    cursor: pointer;
    transition: all .2s;
}

.btnBuy:hover{
    transform: scale(1.1);
}

.preco{
    color: #ba54f5;
    font-weight: bold;
}

/* .preco:hover{
    animation: teste .5s infinite;
} */

@keyframes teste{
    from{
        transform: scale(3);
    }
    to{
        transform: scale(1);
    }
}

@media (max-width: 500px){
    .card{
        font-size: 12px;
        flex-direction: column;
        height: 200px;
        padding: 1rem 0;
    }
}

@media (max-width: 350px){
    .card{
        font-size: 10px;
        flex-direction: column;
        height: 200px;
        padding: 1rem 0;
        width: 200px;
    }
}