.container {
    position: fixed;
    width: calc(100% - 40px);
    max-width: 790px;
    min-height: 200px;
    top: 10px;
    left: 50%;
    transform: translateX(-50%);
    padding: 1rem 0;
    background-color: #171941;
    border: 1px solid #eee;
    z-index: 900;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0px 0px 39px 20px rgba(0, 0, 0, 0.103);
    animation: join .3s forwards ease-in;
    margin: 0 20px 0px 0px;
}

.content {
    display: flex;
    flex-direction: column;
    max-width: 800px;
    width: 100%;
    gap: 10px;
    height: 90%;
    padding: 0 1rem;
    font-family: Poppins;
}

.title{
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-family: sans-serif;
    text-align: center;
    font-weight: 100;
}

.content h3{
    font-weight: 100;
    font-size: 14px;
    margin: 0;
}

.content .title span {
    transform: rotateZ(-45deg);
    font-size: 20px;
    cursor: pointer;
}

.buy{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
    margin: 2rem 0;
}

.containerBuy{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.containerRoleta{
    width: 160px;
    height: 160px;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    align-items: center;
    justify-content: center;
}

.ballBtn{
    width: 160px;
    height: 160px;
    /* background-color: #2c06ff; */
    border-radius: 50%;
    position: absolute;
    top: -4px;
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: all .2s;
}

.circle{
    width: 20px;
    height: 20px;
    background-color: #2c06ff;
    border-radius: 50%;
    transform: translateY(-30%);
    
}

.valueRoleta{
    position: absolute;
    font-size: 2rem;
    font-family: Poppins;
    transform: translateY(-30%);
}

.textRoleta {
    position: absolute;
    transform: translateY(2rem);
    color: #e14eca;

}

.containerInfos{
    max-width: 60%;
}

.containerInfos h2{
    margin: 0;
}

.creditos,
.valor{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 1rem 0;
}

.valor span{
    background: #e14eca;
    padding: 3px;
    border-radius: 5px;
}

.creditos input::-webkit-inner-spin-button,
.creditos input::-webkit-nner-spin-button{
    display: none;
}

.creditos input {
    width: 50px;
    background: none;
    border: 1px solid #bbb;
    color: #bbb;
    padding: .5rem;
    border-radius: 5px;
    -moz-appearance: textfield;
    outline: none;
}

.content button{
    margin: 0;
}

.containerPix{
    display: grid;
    width: 100%;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    margin: 2rem 0;
}

.pixQrCode {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 14px;
    color: #ccc;
}

.pixQrCode img{
    border-radius: 10px;
}

.status {
    background-color: #2c06ff;
    color: #eee;
    padding: 0 1rem;
    border-radius: 10px;
    margin-top: 10px;
}

.pixCopiaCola{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.copiaCola{
    border: none;
    outline: none;
    width: 100%;
    background-color: #151a75;
    padding: 10px;
    color: #ccc;
}

.copiaColaContainer{
    border: 1px solid #1e0996;
    border-radius: 10px;
    overflow: hidden;
    display: grid;
    grid-template-columns: 4fr 1fr;
    box-sizing: border-box;
}

.btnCopy{
    background-color: #1e0996;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0px;
    /* width: 0px; */
}

.containerBuy button {
    margin: 10px 0;
    max-width: 500px;
}

.roletaContainer{
    display: flex;
    justify-content: center;
    align-items: center;
}

.pago{
    background-color: #0bd30b;
}

@media (max-width: 450px){
    .container{
        width: 100vw;
        height: 100vh;
        top: 0;
        overflow-y: scroll;
        padding: 0;
    }

    .buy{
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr;
    }

    .buy{
        max-height: 60vh;
    }

    .containerInfos{
       max-width: 100%;
    }

    .containerPix{
        grid-template-columns: 1fr;
        grid-template-rows: 130px 1fr;
        margin: 0;
    }
    .pixCopiaCola{
        margin: 10px 0;
    }
}

@keyframes join {
    from{
        transform: translate(-50%, -50%);
        opacity: 0;
    }
    to{
        transform: translate(-50%, 0%);
        opacity: 1;
    }
}