*{
    box-sizing: border-box;
}
body{
    margin: 0;
    padding: 0;
}

html {
    scroll-behavior: smooth;
}

.animLeft {
    transform: translateX(-30%);
    opacity: 0;
    animation: AnimLeft .4s forwards;
}

@keyframes AnimLeft{
    to{
        transform: initial;
        opacity: 1;
    }
}

body{
    background-color: #171941;
    background-image: url('./assets/bg-image.png');
    opacity: 1;
    background-size: 20px;
}

.root {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
}
.root::before{
    content: '';
    width: 100%;
    height: 100vh;
    
    z-index: -2;
    background-color: #171941d7;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    
}