.container {
  margin: 1rem;
  width: calc(100% - 2rem);
  display: flex;
  flex-direction: column;
  overflow-x: scroll;
}

.container::-webkit-scrollbar {
  /* width: 5px; */
  background: #00000000;
  height: 5px;
}

.container::-webkit-scrollbar-thumb {
  background-color: #ba54f5;
  border-radius: 30px;
}

.titles {
  font-family: sans-serif;
  color: #aaa;
  display: grid;
  grid-template-columns: 50px 80px 200px repeat(8, 110px);
  font-size: 14px;
  margin: 2rem auto;
  max-width: calc(80px + 200px + 9 * 110px + 40px);
  padding-left: 2.5rem;
  font-weight: bold;
}

.err {
  font-family: Poppins;
  text-align: center;
  font-size: 1.5rem;
  color: #aaa;
  margin: 2rem;
}
