.container{
    display: grid;
    grid-template-columns: 250px auto;
    font-family: Poppins, sans-serif;
    gap: 10px;
    overflow-x: scroll;
}

.nav ul{
    list-style: none;
    
}

.nav ul li a{
    color: #eee;
    text-decoration: none;
    padding: 10px 5px;
    display: block;
    transition: all .3s;
    border-radius: 5px;
}

.nav ul li{
    border-bottom: 1px solid #ba54f579;
    margin: 10px 0;
}

.nav ul li.select a{
    color: #ba54f5;
}

.nav ul li a:hover{
    background-color: rgba(0, 0, 0, 0.2);
}

.container section {
    margin: 1rem 3rem;
    border-radius: 10px;
    color: #aaa;
    /* width: 100%; */
    overflow-x: hidden;
}

@media (max-width: 700px){
    .container {
        display: flex;
        flex-direction: column;
    }

    .container section{
        margin: 10px 10px;

    }
}