.loading {
    width: 50px;
    height: 50px;
    border-style: solid solid none;
    border-color: #eee;
    border-width: 3px 3px 3px 0px;
    border-radius: 50%;
    animation: Loading 1s infinite ease-in;
    position: absolute;
    left: calc(50% - 25px);
    top: 50%;
    transform: translateY(-50%);
    /* transform: translateX(-50%); */
}

@keyframes Loading {
    from{
        transform: rotateZ(0deg);
    }
    to{
        transform: rotateZ(360deg);
    }
} 