.container {
    margin: 1rem;
    width: calc(100% - 2rem);
    display: flex;
    flex-direction: column;
    overflow-x: scroll;
}

.container::-webkit-scrollbar{
    background: #00000000;
    height: 5px;
}

.container::-webkit-scrollbar-thumb {
    background-color: #ba54f5;
    border-radius: 30px;
}

.titles {
    font-family: sans-serif;
    color: #aaa;
    display: grid;
    grid-template-columns: 80px repeat(11, 110px);
    font-size: 14px;
    margin: 2rem auto;
    max-width: calc(80px + 11 * 110px + 40px);
    padding-left: 2.5rem;
    font-weight: bold;
}

.containerCards{
    margin: auto;
    display: grid;
    gap: 10px;
    max-width: 1000px;
    width: 100%;
    grid-template-columns: 1fr 1fr;
}

.err{
    font-size: 1rem;
    font-family: Poppins, sans-serif;
    color: #bbb;
    text-align: center;
}

@media (max-width: 930px){
    .containerCards{
    grid-template-columns: 1fr;

    }
}