.produto {
    display: grid;
    grid-template-columns: 4fr .5fr .5fr .3fr;
    gap: 20px;
    width: 100%;
    min-height: 50px;
    border-radius: 10px;
    border: 1px solid #e14eca;
    align-items: center;
    color: #bbb;
    margin: 10px 0;
}

.name{
    padding-left: 10px;
}   

.quant{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.quantText{
    align-items: center;
    width: 100%;
    padding: 0px 0px;
    display: flex;
    justify-content: center;
}

.quant span{
    font-size: 1.5rem;
    color: #e14eca;
    cursor: pointer;
}

.btnClose{
    transform: rotateZ(-45deg);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    color: #e14eca;
    cursor: pointer;
}