.not{
    max-width: 90vw;
    color: #fff;
    width: 100%;
    border-radius: 10px;
    padding: .5rem;
    margin: 5px auto;
    opacity: 1;
    text-align: center;
    animation: not 1.5s forwards;
    font-family: Poppins, sans-serif;
}

.alert{
    background-image: linear-gradient(to bottom left, #ff0000, #960000);
}

.succes{
    background-image: linear-gradient(to bottom left, #00ff00, #00b800);

}

@keyframes not {
    0%{
        opacity: 0;
        transform: translateY(-30%);
    }
    20%{
        opacity: 1;
        transform: translateY(0%);
    }
    80%{
        opacity: 1;
        transform: translateY(0%);
    }
    100%{
        transform: translateY(-30%);
        opacity: 0;
    }
}