.container{
    width: 100%;
    /* overflow-x: scroll; */
}

.title {
    margin: 1rem;
    font-size: 2rem;
    color: #ddd;
}

.title::after{
    content: '';
    width: 150px;
    height: 3px;
    z-index: -1;
    position: relative;
    background-color: #ba54f5;
    border-radius: 2px;
    display: block;
    top: 5px;
    margin-bottom: 10px;
}

.subTitle{
    margin: 1rem 0;
    color: #ccc;
    font-size: 18px;
}

.historico{
    margin: 2rem;
    display: flex;
    flex-direction: column;
    max-width: 400px;
    box-sizing: border-box;
}

.itens {
    overflow-x: scroll;
    padding-bottom: 10px;
}

.itens::-webkit-scrollbar{
    /* width: 5px; */
    background: #00000000;
    height: 5px;
}

.itens::-webkit-scrollbar-thumb {
    background-color: #ba54f5;
    border-radius: 30px;
}

.titles{
    display: grid;
    grid-template-columns: 200px 80px 100px;
    color: #eee;
    font-size: 18px;
    margin: 10px 0;
}

.loading {
    width: 50px;
    height: 50px;
    border-style: solid solid none;
    border-color: #eee;
    border-width: 3px 3px 3px 0px;
    border-radius: 50%;
    animation: Loading 1s infinite ease-in;
}

@keyframes Loading {
    from{
        transform: rotateZ(0deg);
    }
    to{
        transform: rotateZ(360deg);
    }
} 