.container {
    width: calc(100% - 2rem);

    display: grid;

    grid-template-columns: 1fr 1fr 1fr;

    align-items: center;
    justify-items: center;
    margin: 1rem;
}

@media (max-width: 1024px){
    .container{
    grid-template-columns: 1fr  1fr;

    }
}

@media (max-width: 700px){
    .container{
        grid-template-columns: 1fr;
    }
}
@media (max-width: 350px){
    .container{
        margin: 0;
        width: 100%;
    }

    .container div {
        width: 250px;
    }
}

@media (max-width: 300px){
    .container div {
        width: 200px;
    }

    .container div h2{
        font-size: 14px;
    }
}