.container {
    width: 100%;
    font-family: Poppins;
    padding: 0 1rem;
}

.title {
    text-align: center;
    font-size: 1.5rem;
    color: #eee;
    margin: 1rem 0;
}

.main {
    display: grid;
    grid-template-columns: 2fr 1fr;
    gap: 1rem;
}

.produtos {
    width: 100%;
    min-height: 500px;

    box-shadow: 0px 0px 25px 15px rgba(0, 0, 0, 0.2);
    backdrop-filter: blur(1.5px);
    padding: 10px;
}

.header{
    display: grid;
    grid-template-columns: 4fr .5fr .5fr .3fr;
    gap: 20px;
    color: #eee;
    width: 100%;   
}

.header div:first-child{
    padding-left: 10px;
}   

.infos{
    width: 100%;
    box-shadow: 0px 0px 25px 15px rgba(0, 0, 0, 0.2);
    background-color: rgba(255, 255, 255, 0.1);;
    border-right: 1px solid rgba(255, 255, 255, 0.25);
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    border-top: 1px solid rgba(255, 255, 255, 0.1);
    border-left: 1px solid rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(1.5px);

    overflow: hidden;

    height: 220px;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.titleInfo{
    font-size: 1.2rem;
    color: #eee;
    border-bottom: 1px solid rgba(255, 255, 255, 0.25);
    width: 100%;
    padding: 1rem;
}

.total {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #bbb;
    padding: 1rem;
}

.infos button{
    width: 100%;
    border-radius: 0px;
    transform: scale(1.1);
    margin: 0;
}

.infos button:hover {
    transform: scale(1.2);

}

.msg{
    text-align: center;
    margin: 2rem 0;
    font-size: 1.5rem;
    color: #bbb;
}

.statusBuy{
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    backdrop-filter: blur(10px);
    z-index: 950;
    display: flex;
    justify-content: center;
    align-items: center;
}

.box{
    max-width: 300px;
    width: 100%;
    height: 230px;
    background-color: #121881;
    border-radius: 10px;
    z-index: 951;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.box button{
    max-width: 70%;
    margin: 10px;
}

.box.err{
    background-color: #9e0000;
}

.box.suc{
    background-color: rgb(0, 151, 0);

}

.passar{
    width: 100%;
    color: #aaa;
    padding: 0 1rem;
    display: flex;
    justify-content: space-between;
}

.slide {
    width: 40px;
    height: 20px;
    /* background-color: #958b9b; */
    border-radius: 10px;
    display: flex;
    border: 1px solid #ba54f5;
    box-sizing: content-box;
    overflow: hidden;
    cursor: pointer;
}

.slide div{
    width: 20px;
    height: 20px;
    background-color: #eee;
    /* transform: translateX(100%); */
    z-index: 999;
    position: absolute;
    border-radius: 50%;
    transition: all .3s;
    transform: scale(1.2);
}

.slide.active div{
    transform: scale(1.2) translateX(20px);
}

.slide::after{
    width: 0px;
    content: '';
    /* z-index: -1; */
    height: 40px;

    border-radius: 10px;
    background-color: #ba54f5;
    transition: all .3s;
    border: 1px solid #ba54f5;

}

.slide.active::after{
    width: 50px;
    height: 20px;
    /* margin-left: -9px; */
    transform: scale(1.5);

}

.box div:first-child{
    text-align: center;
    color: #eee;
    font-size: 1.2rem;
    margin-top: 1rem;
}

.checkeds {
    background-color: #0e1252;
    height: 100px;
    width: calc(100% - 2rem);
    border-radius: 10px;    
    overflow-y: scroll;
}

.box .checkeds .ccCheck{
    font-size: 14px;
    font-family: sans-serif;
    text-align: center;
    
}

.box .checkeds .ccCheck:first-child{
    margin-top: 10px;
}

.box .checkeds .ccCheck.ccdid{
    color: #9e0000;
}

.box .checkeds .ccCheck.cclive {
    color: #0f0;
}

@media (max-width: 820px) {
    .main{
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr;
    }
}