.btn{
    padding: .6rem 1.5rem;
    color: rgb(202, 202, 202);
    text-decoration: none;
    font-family: Poppins;
    border-radius: 40px;
    background-color: #1f2251;
    margin: 
    5px 5px;
    transition: all .3s;
    display: inline-block;
}
.btn:hover,
.btn:focus{
    background-color: #232761;
    color: #eee;
}
.btn.active{
    color: rgb(202, 202, 202);
    background: linear-gradient(to right bottom, #ba54f5, #e14eca);
}
