.container{
    width: 100%;
    min-height: calc(100vh - 70px);
    border: #ba54f5 solid 1px;
    border-radius: 10px;
    margin-bottom: 1rem;
    padding: 5px;
}

.containerNav{
    width: calc(100% - 20px);
    height: auto;
    /* height: 80px; */
    margin: auto;
    padding: .5rem 0;
    border-bottom: #ba54f5 solid 1px;
}
.containerNav nav {
    display: flex;
    flex-wrap: nowrap;
    flex-direction: column;
    justify-content: center;
    padding: .5rem;
    height: 100%;
}

.containerNav nav div{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    max-width: 80%;
}

.containerNav .navPrinc {
    display: flex;
    margin: auto;
    flex-wrap: wrap;
    justify-content: space-evenly;
    transform: scale(0%);
    overflow: hidden;
    max-width: 0px;
    flex-wrap: nowrap;
    max-height: 0;
}
.navPrinc.active{
    z-index: 99;
    transition: .01s;
    transform: scale(100%);
    max-width: 100%;
    max-height: 100vh;
    flex-wrap: wrap;

}
.prodList {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);

    width: calc(90% - 20px);

    display: grid;

    transition: all .4s;

    /* grid-template-columns: 1fr 4fr 1fr; */
}

.prodList.escondido{
    transform:  translateX(-100%) scale(0%);
    opacity: 0;
}

.prodList div{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;;
}

.container main {
    overflow: hidden;
    min-height: 80vh;
}
