.saldo{
    max-width: 500px;
    width: calc(100% - 2rem);
    color: #eee;
    font-family: Poppins, sans-serif;
    margin: 1rem;
    display: flex;
    justify-content: space-between;
    margin: 10px auto ;

}

.containerCcs {
    /* margin-top: 10px ; */
    margin: 0px 1rem ;
    width: calc(100% - 2rem);
    padding: 10px 0;
    display: flex;
    flex-direction: column;
    overflow-x: scroll;
}

.containerCcs::-webkit-scrollbar{
    /* width: 5px; */
    background: #00000000;
    height: 5px;
}

.containerCcs::-webkit-scrollbar-thumb {
    background-color: #ba54f5;
    border-radius: 30px;
}

.titles {
    border-bottom: 1px solid rgba(87, 87, 87, 0.521);
    padding-bottom: 2rem;
    font-family: sans-serif;
    color: #aaa;
    display: grid;
    grid-template-columns: 80px repeat(10, 110px);
    font-size: 14px;
    margin: 1.5rem auto 0px ;
    max-width: calc(80px + 10 * 110px + 40px);
    padding-left: 2.5rem;
    font-weight: bold;
}

.err{
    text-align: center;
    color: #bbb;
    font-family: Poppins;
    font-size: 1.2rem;
}

.aviso{
    color: #aaa;
    background-color: rgba(0, 0, 0, 0.5);
    padding: 5px 2rem;
    text-align: center;
    margin: auto;
    font-family: Poppins, sans-serif;
    border-radius: 10px;
    max-width: 500px;
}

.nav{
    display: flex;
    margin: auto;
}

.nav div{
    font-family: Poppins;
    width: 25px;
    height: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 5px;
    color: #eee;
    border-radius: 2px;
    background-color: #ba54f53b;
    transition: all .3s;
    cursor: pointer;
}

.nav a {
    text-decoration: none;
}

.nav div.active{
    background-color: #ba54f5;
    transform: scale(1.1);
}