@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap');
.regra{
    color: #eee;
    background: #232761;
    /* border-radius: 10px; */
    transition: all .5s;
    overflow: hidden;
    /* margin: 15px 0; */
    padding: 0 .5rem;
    transition: all .5s;
    box-shadow: 0px 0px 5px 2px #0000002c;
} 
.title{
cursor: pointer;
    margin-bottom: 1rem;
}
.title h3{
    font-family: Poppins, Arial, Helvetica, sans-serif;
    font-size: 1rem;
    font-weight: 600;
}
.regra div{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.text{
    transition: all .5s;
    font-family: Poppins, sans-serif;
    font-size: .875rem;
    font-weight: 400;
    margin-bottom: 1rem;
    color: #aaa;
}
.text.inative{
    max-height: 0px;
    margin-bottom: 0px;


}