.item {
    display: grid;
    grid-template-columns: 40px 150px 50px;
    align-items: center;
    padding: 1rem 0;
    color: #bbb;
    font-family: Poppins;
    transform: translateX(-20px);
}

.btn{
    background: linear-gradient(to right bottom ,#e14eca,#ba54f5,#e14eca);
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    transition: all .3s;
}

.btn:hover{
    transform: translateY(-5%);
    cursor: pointer;
}

.noStock{
    opacity: 0.7;
}

.noStock .btn:hover{
    transform: translateY(0%);
    cursor: inherit;
}